.main{
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center; 
    box-shadow: 3px 3px  10px black;
    border: 2px solid rgb(82, 80, 80);
}
.form{
    box-shadow: 8px 8px 8px black;
    display: flex;
    flex-direction: column;
    align-self: justify-between;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 80%;
    margin-left: 150px;
    border: 2px solid gray;
   
    background-position: center;

}

.button{
    width: 30%;
    margin-bottom: 20px;
}